.Graphic {
  margin: 10px auto;
  position: relative; }
  .Graphic img {
    left: 0;
    position: absolute;
    top: 0; }

.small-pizza {
  height: 133px;
  width: 200px; }

.medium-pizza {
  height: 200px;
  width: 300px; }

.large-pizza {
  height: 266px;
  width: 400px; }

@media screen and (max-width: 420px) {
  .Graphic {
    margin: 5px auto; }
  .large-pizza {
    width: 365px; } }



